/* Retro Personal Homepage Style - GeoCities Era */
.browser-viewport div#about-me {
  background: linear-gradient(180deg, #f0e6ff 0%, #e6f5ff 100%);
  color: #333333;
  font-family: 'Arial', 'Times New Roman', serif;
  padding: 20px;
  min-height: 100%;
  position: relative;
}

/* Wavy pattern background */
.browser-viewport div#about-me::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image:
    repeating-linear-gradient(
      45deg,
      transparent,
      transparent 15px,
      rgba(150, 100, 255, 0.25) 15px,
      rgba(150, 100, 255, 0.25) 30px
    ),
    repeating-linear-gradient(
      -45deg,
      transparent,
      transparent 15px,
      rgba(100, 150, 255, 0.2) 15px,
      rgba(100, 150, 255, 0.2) 30px
    );
  pointer-events: none;
}

/* Welcome Banner */
.browser-viewport div#about-me .page-content h1 {
  background: linear-gradient(135deg, #0066cc, #6600cc);
  color: white;
  padding: 15px;
  text-align: center;
  font-size: 28px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
  margin-bottom: 20px;
  border: 3px ridge #c0c0c0;
}

.browser-viewport div#about-me .page-content h2 {
  text-align: center;
  color: #0000ff;
  text-decoration: underline;
  font-family: 'Arial', sans-serif;
  font-weight: bold;
}

.browser-viewport div#about-me .page-content h3 {
  background: linear-gradient(90deg, #e0e0ff, #ffe0e0);
  padding: 8px;
  text-align: center;
  color: #333333;
  font-weight: bold;
  border: 2px solid #808080;
  margin: 15px 0;
}

.browser-viewport div#about-me .page-content p {
  background: #ffffff;
  border: 1px solid #cccccc;
  padding: 12px;
  margin: 10px 0;
  color: #333333;
  font-size: 14px;
  line-height: 1.6;
}

/* Visitor Counter - removed in favor of the standard visitor counter component */

/* Web Awards Section */
.web-awards {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 10px;
  margin: 20px 0;
  padding: 10px;
  background: #f9f9f9;
}

.award-badge {
  width: 88px;
  height: 31px;
  background: linear-gradient(45deg, #ffd700, #ffed4e);
  border: 2px solid #000000;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 8px;
  font-weight: bold;
  color: #000000;
  animation: sparkle 2s linear infinite;
}

@keyframes sparkle {
  0%,
  100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
}

/* MIDI Player Widget */
.midi-player {
  background: #c0c0c0;
  border: 2px solid;
  border-color: #ffffff #808080 #808080 #ffffff;
  padding: 5px;
  margin: 15px auto;
  width: 200px;
  text-align: center;
  font-family: 'MS Sans Serif', sans-serif;
  font-size: 11px;
  color: #000000;
}

.midi-player::before {
  content: 'j Now Playing: about_me.mid j';
  display: block;
  margin-bottom: 5px;
}

/* Button styling */
.browser-viewport div#about-me .page-content button {
  background: linear-gradient(to bottom, #ffffff, #c0c0c0);
  border: 2px solid;
  border-color: #ffffff #808080 #808080 #ffffff;
  color: #000000;
  font-family: 'MS Sans Serif', sans-serif;
  padding: 5px 15px;
  cursor: pointer;
  font-size: 11px;
}

.browser-viewport div#about-me .page-content button:active {
  border-color: #808080 #ffffff #ffffff #808080;
}

.browser-viewport div#about-me .page-content button:hover {
  background: linear-gradient(to bottom, #ffffcc, #c0c0c0);
}

/* Webring navigation */
.webring {
  text-align: center;
  margin-top: 20px;
  padding: 10px;
  border: 1px solid #808080;
  background: #f0f0f0;
}

.webring-text {
  color: #666666;
  font-size: 10px;
  font-family: 'Courier New', monospace;
}

/* Guestbook link */
.guestbook-link {
  display: inline-block;
  margin: 20px auto;
  text-align: center;
  animation: pulse 1s ease-in-out infinite;
}

@keyframes pulse {
  0%,
  100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
}

/* Email me GIF button */
.email-me-button {
  display: inline-block;
  width: 88px;
  height: 31px;
  background: linear-gradient(90deg, #ff0000, #ffff00, #00ff00, #00ffff, #0000ff, #ff00ff, #ff0000);
  background-size: 200% 100%;
  animation: rainbow-slide 2s linear infinite;
  border: 2px solid #000000;
  color: #ffffff;
  text-shadow: 1px 1px 2px #000000;
  font-weight: bold;
  font-size: 10px;
  line-height: 27px;
  text-align: center;
  cursor: pointer;
}

@keyframes rainbow-slide {
  0% {
    background-position: 0% 50%;
  }
  100% {
    background-position: 200% 50%;
  }
}
